<template>
  <div class="management scroll">
    <div class="top">
      <p class="top-title color-title"></p>
      <p class="right">
        <span class="color-blue mr8"></span>
        <span class="color-blue mr8"></span>
        <span class="color-blue mr8"></span>
        <span>!</span>
      </p>
    </div>
    <div class="left-content scroll">
      <div class="left-title"></div>
      <div class="left-detail mt40">
        <p class="list-title" @click="showMenu=!showMenu"><img src="../assets/img/menu.jpg" class="folder-icon right" /></p>
        <div v-show="showMenu==true">
          <!--  -->
          <div class="list-unit">
            <p @click="showUser=!showUser"><a-icon type="caret-down" class="arrow-icon" />
            </p>
            <div v-show="showUser==true">
              <p :class="{'unit-active':listUnit==''}"  class="pl30"  @click="listUnit='';add('usermanagement','')" ></p>
              <p :class="{'unit-active':listUnit=='blacklist'}" class="pl30" @click="listUnit='blacklist';add('blacklist','')" ></p>
              <p :class="{'unit-active':listUnit=='liarstatistics'}" class="pl30"  @click="listUnit='liarstatistics';add('liarstatistics','')"></p>
            </div>
            <!--  -->
            <p :class="{'unit-active':listUnit=='sellermanagement'}" @click="listUnit='sellermanagement';add('sellermanagement','')"></p>
            <p :class="{'unit-active':listUnit=='shopmanagement'}" @click="listUnit='shopmanagement';add('shopmanagement','')"></p>
            <p :class="{'unit-active':listUnit=='taskmanagement'}" @click="listUnit='taskmanagement';add('taskmanagement','')"></p>
            <p :class="{'unit-active':listUnit=='ordermanagement'}" @click="listUnit='ordermanagement';add('ordermanagement','')"></p>
            <p :class="{'unit-active':listUnit=='rechargemanagement'}" @click="listUnit='rechargemanagement';add('rechargemanagement','')"></p>
          </div>
          <!--  -->
          <div class="list-unit">
            <p @click="showCash=!showCash"><a-icon type="caret-down" class="arrow-icon" /></p>
            <div v-show="showCash==true">
              <p :class="{'unit-active':listUnit=='cashmanagement'}" class="pl30"  @click="listUnit='cashmanagement';add('cashmanagement','')"></p>
            </div>
          </div>
          <!--  -->
          <div class="list-unit">
            <p @click="showSubstation=!showSubstation">  <a-icon type="caret-down" class="arrow-icon" /> </p>
            <div v-show="showSubstation==true">
              <p :class="{'unit-active':listUnit=='substationmanagement'}"  class="pl30" @click="listUnit='substationmanagement';add('substationmanagement','')"></p>
              <p :class="{'unit-active':listUnit=='myteam'}"  class="pl30"  @click="listUnit='myteam';add('myteam','')"></p>
              <p :class="{'unit-active':listUnit=='teamsetting'}" class="pl30"  @click="listUnit='teamsetting';add('teamsetting','')"></p>
            </div>
          </div>
          <!--  -->
          <div class="list-unit">
            <p @click="showDataMsg=!showDataMsg"><a-icon type="caret-down" class="arrow-icon" /></p>
            <ul v-show="showDataMsg==true">
              <li  class="pl30" :class="{'unit-active':listUnit=='orderstatistics'}" @click="listUnit='orderstatistics';add('orderstatistics','')"></li>
              <li  class="pl30" :class="{'unit-active':listUnit=='statistics'}" @click="listUnit='statistics';add('statistics','')"></li>
              <li  class="pl30" :class="{'unit-active':listUnit=='moneystatistics'}" @click="listUnit='moneystatistics';add('moneystatistics','')"></li>
              <li  class="pl30" :class="{'unit-active':listUnit=='substationstatistics'}" @click="listUnit='substationstatistics';add('substationstatistics','')"></li>
            </ul>
          </div>
          <!--  -->
          <div class="list-unit">
            <p :class="{'unit-active':listUnit=='systemmsg'}" @click="listUnit='systemmsg';add('systemmsg','')"></p>
            <p :class="{'unit-active':listUnit=='appealcenter'}" @click="listUnit='appealcenter';add('appealcenter','')"></p>
          </div>
          <div class="list-unit">
            <p @click="systemSetting=!systemSetting"> <a-icon type="caret-down" class="arrow-icon"/></p>
            <div v-show="systemSetting==true">
              <p  :class="{'unit-active':listUnit=='systemsetting'}" class="pl30"  @click="listUnit='systemsetting';add('systemsetting','')"></p>
              <p  :class="{'unit-active':listUnit=='bankmanagement'}" class="pl30"  @click="listUnit='bankmanagement';add('bankmanagement','')"></p>
              <p  :class="{'unit-active':listUnit=='editioncontrol'}" class="pl30"  @click="listUnit='editioncontrol';add('editioncontrol','APP')">APP</p>
            </div>
          </div>
          <!--  -->
          <div class="list-unit">
            <!-- <p :class="{'unit-active':listUnit=='index'}" @click="listUnit='index';add('')">
              <a-icon type="home" />
            </p>-->
            <p :class="{'unit-active':listUnit=='administrators'}" @click="listUnit='administrators';add('administrators','')"></p>
          </div>
          <!--  -->
          <div class="list-unit">
            <p @click="showSetting=!showSetting"><a-icon type="caret-down" class="arrow-icon" /></p>
            <ul v-show="showSetting==true">
              <li  :class="{'unit-active':listUnit=='rolemanagement'}"  class="pl30" @click="listUnit='rolemanagement';add('rolemanagement','')">
                <a-icon type="team" />
              </li>
              <li class="pl30" @click="showQuanxian=!showQuanxian">
                <a-icon type="appstore" />
                <a-icon type="caret-down" class="arrow-icon" />
              </li>
              <ul v-show="showQuanxian==true">
                <li :class="{'unit-active':listUnit=='powermanagement'}"  class="pl50"  @click="listUnit='powermanagement';add('powermanagement','')"></li>
                <li :class="{'unit-active':listUnit=='userpowermanagement'}" class="pl50"  @click="listUnit='userpowermanagement';add('userpowermanagement','')"></li>
                <li :class="{'unit-active':listUnit=='rolepowermanagement'}" class="pl50" @click="listUnit='rolepowermanagement';add('rolepowermanagement','')"></li>
              </ul>
              <li  :class="{'unit-active':listUnit=='menumanagement'}" class="pl30"  @click="listUnit='menumanagement';add('menumanagement','')">  <a-icon type="form"/></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="right-content">
      <a-tabs   hideAdd  v-model="activeKey"   type="editable-card" @edit="onEdit"  @change="callback"  defaultActiveKey="1">
        <a-tab-pane v-for="pane in panes" :tab="pane.title" :key="pane.key" :closable="pane.closable">{{pane.content}}</a-tab-pane>
      </a-tabs>
      <div class="right-content2">
          <router-view  @childData="getMsgFormChild"></router-view>
      </div>
    </div>
    <div class="bottom-content"></div>
  </div>
</template>

<script>

export default {
  name: "index",
  data() {
    return {
      showMenu: false,
      showUser: false,
      showSubstation: false,
      showCash: false,
      showSetting: false,
      systemSetting: false,
      showDataMsg: false,
      showQuanxian: false,
      listUnit: "usermanagement",
      targetKey: "usermanagement",
      activeKey: "usermanagement",
      panes: [],
      childData: "",
      path:''
    };
  },
  mounted() {
    // 
    this.panes = [
      { title: "", content: "", key: "usermanagement", closable: false }
    ];
  },
  methods: {
    callback(targetKey) {
      this.$router.push({
        path:'/'+ targetKey
      });
    },
    add(path,title) {
      this.path = path;
      this.$router.push({
        path: '/'+path
      });
      const panes = this.panes;
      var flag = 0;
      const activeKey = path;
      panes.forEach(item => {
        if (title === item.title) {
          flag++;
          this.activeKey = activeKey;
        }
      });
      if (flag == 0) {
        if (path == "usermanagement") {
          panes.push({
            title: title,
            content: "",
            key: activeKey
          });
        } else {
          panes.push({
            title: title,
            content: "",
            key: activeKey
          });
        }
        this.panes = panes;
        this.activeKey = activeKey;
      }
    },
    onEdit(targetKey, action) {
      this[action](targetKey);
    },
    remove(targetKey) {
      let activeKey = this.activeKey;
      let lastIndex;
      this.panes.forEach((pane, i) => {
        if (pane.key === targetKey) {
          lastIndex = i - 1;
        }
      });
      const panes = this.panes.filter(pane => pane.key !== targetKey);
      if (panes.length && activeKey === targetKey) {
        if (lastIndex >= 0) {
          activeKey = panes[lastIndex].key;
        } else {
          activeKey = panes[0].key;
        }
      }
      this.panes = panes;
      this.activeKey = activeKey;
      // if (activeKey == "") {
      //   this.path = "/blacklist";
      // } else if (activeKey == "") {
      //   this.path = "/usermanagement";
      // }
      this.$router.push({
        path:'/'+this.activeKey
      });
    },
    getMsgFormChild(path,title) {
      this.add(path,title);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



